/* Общие стили для всего приложения */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 100%;
  margin: 0 auto;
}

/* Счёт */
.score-board p {
  font-size: 1.2em;
}

.energy-board {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.energy-icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.image-container {
  margin-top: 20px;
}

.clickable-image {
  width: 100%;
  max-width: 250px;
  height: auto;
  cursor: pointer;
}

/* Кнопки прокачки */
.upgrade-container button, .energy-upgrade-button {
  padding: 12px 24px;
  font-size: 1.1em;
  margin: 10px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  background-color: #4CAF50;
  color: white;
}

.upgrade-container button:hover, .energy-upgrade-button:hover {
  background-color: #45a049;
}

/* Стили для мобильных */
@media (max-width: 600px) {
  .score-board p {
    font-size: 1em;
  }

  .clickable-image {
    max-width: 200px;
  }

  .App {
    padding: 10px;
  }

  .energy-board p {
    font-size: 1em;
  }

  .upgrade-container button, .energy-upgrade-button {
    width: 100%; /* Кнопки на мобильных должны быть на всю ширину */
    font-size: 1em;
  }

  h1 {
    font-size: 1.5em;
  }
}
